.profile {
    padding: 10px;
    display:flex;
}
.profileImage {
    width: 100%;
    height: 20px;
}
.profilePhoto {
    margin-right: 10px;
}
.profilePhoto img {
    width: 100px;
    height: 100px;
}
.profileInfo div {
    margin-bottom: 10px;
}

.profileInfo span {
    font-weight: 700;
}

.contactsLink {
    color: #0000FFFF;
    cursor: pointer;
}

.contactsLink:hover {
    color: #0000c1;
}